import { useMediaQuery } from '@mui/material'
import { useTheme } from '@mui/material/styles'
import React from 'react'
import { HeaderFAQ } from './HeaderFAQ'
import AppbarMobile from './HeaderFAQPhone'
import { HeaderWoordenDesktop } from './HeaderWoordenDesktop'
import HeaderWoordenPhone from './HeaderWoordenPhone'

export default function HeaderWoordenMatches() {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.down('md'))

  return <>{matches ? <HeaderWoordenPhone /> : <HeaderWoordenDesktop />}</>
}
