import * as React from 'react'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'

export const TextLanding = () => {
  return (
    <Typography>
      <Box
        sx={{
          fontSize: 16,
          fontFamily: 'Source Serif Pro',
          fontWeight: 600,
          color: 'black',
          maxWidth: { xs: 300, md: 500 },
        }}
      >
        Hier uitleg over de Software Development life cycle Eventueel ook nog
        een deel uitleg over Enablement of over de app?
      </Box>
    </Typography>
  )
}
