import * as React from 'react'
import { Grid, Container } from '@mui/material'
import SubHeader from 'src/components/SubHeader'
import { Footer } from 'src/components/Footer'
import { SearchFAQ } from 'src/components/SearchFAQ'
import { TextFAQ } from 'src/components/TextFAQ'
import { SubjectTab } from 'src/components/SubjectTab'
import AccordionSub from 'src/components/AccordionSub'
import TextForm from 'src/components/TextForm'
import Form from 'src/components/Form'
import { HeaderFAQ } from 'src/components/headers/HeaderFAQ'
import AppbarMatches from 'src/components/headers/HeaderFAQMatches'

export const FAQ = () => {
  return (
    <>
      <AppbarMatches />
      <SubHeader />
      <Container>
        <Grid container marginTop={12} justifyContent='Center'>
          <Grid item md={10}>
            <SearchFAQ />
          </Grid>
        </Grid>
        <Grid container marginTop={8} justifyContent='Center'>
          <Grid item md={6}>
            <TextFAQ />
          </Grid>
        </Grid>
        <Grid container marginTop={8} justifyContent='Center'>
          <SubjectTab />
        </Grid>
        <Grid container columnSpacing={2}>
          <Grid item xs={12} md={6} marginTop={8}>
            <AccordionSub />
          </Grid>
          <Grid item xs={12} md={6} marginTop={8}>
            <AccordionSub />
          </Grid>
        </Grid>
        <Grid container marginTop={8} justifyContent='Center' xs={12}>
          <TextForm />
        </Grid>
        <Grid
          container
          marginTop={8}
          marginBottom={16}
          justifyContent='Center'
          xs={12}
        >
          <Form />
        </Grid>
      </Container>
      <Footer />
    </>
  )
}
