import * as React from 'react'
import { Grid, Box } from '@mui/material'
import Typography from '@mui/material/Typography'

export const Design = () => {
  return (
    <Box>
      <Typography
        variant='h4'
        gutterBottom
        component='div'
        paddingBottom={10}
        align='left'
        ml={6}
      >
        1. Specify
      </Typography>
      <Typography ml={6} variant='h6' gutterBottom component='div' align='left'>
        Introductie
      </Typography>
      <Typography ml={6} variant='body2' gutterBottom align='left'>
        In deze stap worden de eisen verder verhelderd en uitgewerkt. Dit wordt
        gedaan door de Enterprise Architect samen met de Product Owner en evt.
        de Solution architect. Hierbij wordt ook een Informatie Analist
        ingeschakeld om de functionele eisen en wensen goed vast te stellen en
        de informatie stromen in beeld te brengen. Wellicht wordt er in deze
        fase een hoog over (kosten)schatting gemaakt met de Solution Architect
        (T-Shirt). De juiste Prioriteit van de Epic wordt bepaald en aangepast
        op de Roadmap. Afstemming met andere POs over afhankelijkheden en
        planning.
      </Typography>
      <Box
        my={8}
        ml={6}
        sx={{
          width: '100%',
          height: 100,
          backgroundColor: 'primary.dark',
          maxWidth: { xs: 350, md: 1000 },
        }}
      />
      <Grid my={4} container spacing={3}>
        <Grid item xs={12} md={4}>
          <Typography
            ml={6}
            variant='h6'
            gutterBottom
            component='div'
            align='left'
            paddingLeft='30px'
          >
            Input
          </Typography>
          <Typography ml={6} variant='body2' gutterBottom align='left'>
            <ul>
              <li>Input hypothesis statement</li>
              <ul>
                <li>Value statement</li>
                <li>NFRs</li>
              </ul>
              <li>Features</li>
              <ul>
                <li>Korte beschrijving</li>
                <li>Benefit hypothesis</li>
                <li>Acceptatie criteria</li>
              </ul>
              <li>Testplan overall project</li>
              <li>
                Product risicoanalyise overall per project (managementniveau,
                kwaliteitskenmerken volgens ISO25010
              </li>
            </ul>
          </Typography>
        </Grid>
        <Grid item xs={12} md={4}>
          <Typography
            ml={6}
            variant='h6'
            gutterBottom
            component='div'
            align='left'
            paddingLeft='30px'
          >
            Activiteiten
          </Typography>
          <Typography
            ml={6}
            variant='body2'
            gutterBottom
            align='left'
            paddingLeft='30px'
          >
            <ul>
              <li>Verzamelen en analyseren van de eisen</li>
              <li>Verder uitwerken van de eisen</li>
              <li>
                Valideren van de eisen op realisme, consitentie en compleetheid
              </li>
              <li>Bepalen van de Functionele oplossing richting</li>
              <li>Maken van een customer journey (igv online type epic)</li>
            </ul>
          </Typography>
        </Grid>
        <Grid item xs={12} md={4}>
          <Typography
            ml={6}
            variant='h6'
            gutterBottom
            component='div'
            align='left'
            paddingLeft='40px'
          >
            Output
          </Typography>
          <Typography
            ml={6}
            variant='body2'
            gutterBottom
            align='left'
            paddingLeft='35px'
          >
            <ul>
              <li>In/Uitgaande stromen (UML component diagram)</li>

              <li>Data model (UML class diagram)</li>

              <li>Andere UML diagrammen, bv:</li>
              <ul>
                <li>Use cade model</li>
                <li>Sequence diagram</li>
              </ul>
              <li>Functionele Platen van EA (Archimate)</li>
              <li>Customer Journey</li>
            </ul>
          </Typography>
        </Grid>
      </Grid>
    </Box>
  )
}
