import * as React from 'react'
import { Grid, Box } from '@mui/material'
import { ImageLanding } from 'src/components/ImageLanding'
import { LogoLanding } from 'src/components/LogoLanding'
import { SearchLanding } from 'src/components/SearchLanding'
import { StartBtn } from 'src/components/StartBtn'
import { TextLanding } from 'src/components/TextLanding'
import { TitelLanding } from 'src/components/TitelLanding'
import { VragenBtn } from 'src/components/VragenBtn'
import { WoordenBtn } from 'src/components/WoordenBtn'
import { FooterLanding } from 'src/components/FooterLanding'

export const Landing = () => {
  return (
    <>
      <Grid container marginLeft={8} marginTop={8}>
        <Grid item xs={12} md={4} justifyContent='start'>
          <Box sx={{ marginLeft: { xs: 6 } }}>
            <LogoLanding />
          </Box>
        </Grid>
      </Grid>
      <Grid container marginLeft={8} marginTop={8}>
        <Grid item xs={12} md={7}>
          <TitelLanding />
        </Grid>
        <Grid item xs={12} md={5} marginTop={6}>
          <SearchLanding />
        </Grid>
      </Grid>
      <Grid container marginLeft={8} marginTop={8}>
        <Grid item xs={12} md={6} marginBottom={5}>
          <TextLanding />
        </Grid>
        <Grid item xs={12} md={6}>
          <Box
            sx={{
              paddingBottom: { xs: 50, md: 0 },
              marginLeft: { xs: -2 },
            }}
          >
            <ImageLanding />
          </Box>
        </Grid>
      </Grid>
      <Grid container marginLeft={8} marginTop={-40}>
        <Grid
          item
          xs={12}
          md={2}
          marginBottom={5}
          sx={{ marginLeft: { xs: 6 } }}
        >
          <WoordenBtn />
        </Grid>
        <Grid item xs={12} md={3} sx={{ marginLeft: { xs: 3 } }}>
          <VragenBtn />
        </Grid>
      </Grid>
      <Grid container marginLeft={8} marginTop={16}>
        <Grid item xs={12} md={4} justifyContent='start'>
          <StartBtn />
        </Grid>
      </Grid>
      <FooterLanding />
    </>
  )
}
