import * as React from 'react'
import SubHeader from 'src/components/SubHeader'
import { Alphabet } from 'src/components/Alphabet'
import { SearchAppBar } from 'src/components/Search'
import { Footer } from 'src/components/Footer'
import { SearchResults } from 'src/components/SearchResults'
import { Paper, Grid, Box, Container, Typography } from '@mui/material'
import HeaderWoordenMatches from 'src/components/headers/HeaderWoordenMatches'

export const Glossary = () => {
  return (
    <>
      <HeaderWoordenMatches />
      <SubHeader />
      <Container sx={{ marginLeft: { md: -7 } }}>
        <Grid container>
          <Grid item xs={12} md={5}>
            <Paper
              elevation={4}
              sx={{
                height: '100%',
                marginLeft: { xs: 0, md: '0%' },
                width: { md: 750 },
              }}
            >
              <Typography
                align='left'
                padding='40px'
                fontSize={40}
                fontWeight='bold'
                paddingTop='-20px'
                paddingLeft='50px'
                marginTop='40px'
              >
                Titel
              </Typography>
              <SearchAppBar />
              <Alphabet />
            </Paper>
          </Grid>
          <Grid item xs={12} md={7}>
            <SearchResults />
          </Grid>
        </Grid>
      </Container>
      <Footer />
    </>
  )
}
