import * as React from 'react'
import { Design } from 'src/components/Design'
import { Footer } from 'src/components/Footer'
import { Picture } from 'src/components/Picture'
import { StepperImage } from 'src/components/StepperImage'
import SubHeader from 'src/components/SubHeader'
import { Grid, Divider, Stack } from '@mui/material'
import HeaderContentMatches from 'src/components/headers/HeaderContentMatches'

export const Content = () => {
  return (
    <>
      <HeaderContentMatches />
      <SubHeader />
      <Grid my={4} container spacing={2}>
        <Grid
          item
          xs={12}
          md={5}
          container
          direction='row'
          justifyContent='center'
        >
          <Stack marginBottom={4}>
            <Picture />

            <StepperImage />
          </Stack>
        </Grid>
        <Divider flexItem orientation='vertical' />
        <Grid item xs={12} md={6}>
          <Design />
        </Grid>
      </Grid>
      <Grid my={4} container spacing={2}>
        <Grid item xs={12} md={5}></Grid>
        <Divider flexItem orientation='vertical' />
        <Grid item xs={12} md={6}>
          <Design />
        </Grid>
      </Grid>

      <Footer />
    </>
  )
}
