import Box from '@mui/material/Box'
import Paper from '@mui/material/Paper'
import TextField from '@mui/material/TextField'
import { Grid, Container } from '@mui/material'
import React, { useState } from 'react'
import ButtonFAQ from './ButtonFAQ'

export default function Form() {
  // const classes = useStyles()
  const [name, setName] = useState('')
  const [team, setTeam] = useState('')
  const [question, setQuestion] = useState('')

  return (
    <Box
      sx={{
        display: 'flex',
        flexWrap: 'wrap',
        '& > :not(style)': {
          m: 1,
          maxWidth: { xs: 300, sm: 626 },
          // width: 626,
          // height: 545,
        },
      }}
    >
      <Paper elevation={2}>
        <Container>
          <Grid container maxWidth='lg' display='flex'>
            <form noValidate autoComplete='off' style={{ width: 550 }}>
              <Grid item xs={12} marginTop={2} justifyContent='Center'>
                <TextField
                  label='Naam'
                  variant='standard'
                  fullWidth
                  defaultValue='Voorbeeld'
                  style={{ width: '100%' }}
                  onChange={event => setName(event.target.value)}
                />
              </Grid>
              <Grid marginTop={2} justifyContent='Center'>
                <TextField
                  label='Team'
                  variant='standard'
                  fullWidth
                  defaultValue='Voorbeeld'
                  onChange={event => setTeam(event.target.value)}
                  style={{ width: '100%' }}
                />
              </Grid>
              <Grid marginTop={2}>
                <TextField
                  label='Vraag'
                  variant='standard'
                  fullWidth
                  multiline
                  rows={10}
                  style={{ width: '100%' }}
                  onChange={event => setQuestion(event.target.value)}
                />
              </Grid>
              <Grid container justifyContent='center'>
                <Grid item marginTop={4} marginBottom={4}>
                  <ButtonFAQ />
                </Grid>
              </Grid>
            </form>
          </Grid>
        </Container>
      </Paper>
    </Box>
  )
}
