import * as React from 'react'
import { Box } from '@mui/system'

export const ImageLanding = () => {
  return (
    <Box
      className='ImageLanding'
      component='img'
      alt='circle'
      src='./images/circle.png'
      sx={{ maxHeight: { xs: 305, md: 480 }, maxWidth: { xs: 331, md: 500 } }}
    />
  )
}
