import { useMediaQuery } from '@mui/material'
import { useTheme } from '@mui/material/styles'
import React from 'react'
import { HeaderContentDesktop } from './HeaderContentDesktop'
import HeaderContentPhone from './HeaderContentPhone'

export default function HeaderContentMatches() {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.down('md'))

  return <>{matches ? <HeaderContentPhone /> : <HeaderContentDesktop />}</>
}
